import businessMainPage from '../../components/businessMainPage'
import statisticsPage from '../../components/statisticsPage'
export default {
    data() {
        return {
            currentTab: 'home', //home 首页 ，statistics 统计，loginOut 联系
        }
    },
    components: {
        businessMainPage,
        statisticsPage
    },
    methods: {
        chageTab (val) {
            this.currentTab = val
        },
        loginOut () {
            uni.navigateTo({
                url: '../login/login?pageType=loginOut',
            });
        }
    },
}
